@media only screen and (max-width: 600px) {
  .brand-logo > img {
    height: 50px;
    margin: 5px 10px!important;
  }
  #search{
    width: 100%!important;
    padding-left: 3rem;
  }
  .input-field input[type=search] ~ .material-icons{
    right:3rem!important;
  }
}
:root {
  --bg-dark: #0d1117;
}

.bg-dark {
  background-color: var(--bg-dark);
}

.brand-logo > img {
  height: 50px;
  margin: 5px 30px;
}

nav form {
  padding: 5px 0;
  width: 50%;
  margin: 0 auto;
}
nav form i {
  line-height: calc(64px - 14px) !important;
}

#search {
  border-radius: 0.4em;
  color: white;
  cursor: pointer;
}
#search:focus {
  background-color: #323333;
}

#main {
  /* padding-bottom: 2.5rem; */
  height: calc(100vh - (64px + 2.9rem)) ;
  overflow-y: scroll;
}
#footer p {
}
